
  .app-wrapper {
    background-color: #151932;
    width: 25%;
    min-width: 450px;
    height: 400px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 5px;
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.4);
  }
  
  .header {
    color: rgb(250, 250, 250);
    text-align: center;
    margin: 50px 20px 60px 20px;
   
  }
  
  .main {
    display: flex;
    flex-direction: column;
    align-items: space-between;
    margin-bottom: 50px;
    width: 100%;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .list {
    width: 90%;
    margin: auto;
    max-height: 300px;
    overflow: hidden;
    overflow-y: auto;
  }
  
  /* width */
  ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #aaa;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #444;
    border-radius: 10px;
  }
  
  .task-input {
    width: 350px;
    min-width: 250px;
    padding: 15px 10px 10px 20px;
    margin-right: 10px;
    font-family: 'Josefin Sans', serif;
    font-size: 15px;
    color: #ccc;
    background-color: #151932;
    border: 1px solid #444;
    outline: none;
    border-radius: 20px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    margin-bottom: 1rem;
  }
  
  .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .btn {
    margin: 10px;
  }
  
  .add-task-btn {
    min-width: 100px;
    background-color:rgb(34,193,195);;
    color:white;
    border: none;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    outline: none;
    cursor: pointer;
  }
  
  .list-item {
    display: flex;
    justify-content: space-between;
  }
  
  .list-item span {
    color:white;
  }
  
  .task-btn {
    cursor: pointer;
    background-color: transparent;
    border: none;
    outline: none;
  }
 
  .btn-delete {
    font-size: 16px;
    color: #bbb;
    margin-right: 20px;
  }
  
  .btn-edit  {
    font-size: 16px;
    color: #bbb;
    margin-right: 20px;
  }
  
  .no-tasks {
    color: #777;
    text-align: center;
    font-size: 18px;
    margin-top: 20px;
  }

  .center {
    justify-content: center;
  }

  .play-container {
    background-color: white;
    display:flex;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  }

  .no-task-color {
    color:white;
    text-align: center;
  }

  .date-task {
    justify-content: end;
  }

  .play-container > span {
    margin-top:6px;
  }

  .container-task {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media only screen and (max-width: 850px) {

    .container-task {
      flex-direction: column;
      
    }

    .center {
      justify-content: flex-start;
    }

    .input-wrapper input {
      width:20px;
      height:20px;
    }

    .navbar {
      height: 25px;
      padding-left: 0.5rem
    }

    .task-btn {
      margin-right:12px
    }

    .task-input {
      width:250px;
    }

    .container h1 {
      font-size: 1.8rem;
    }

    .button-wrapper {
      padding:0.1rem;
    }

    .whiteFont {
      display:none;
    }
    
  }
 